import {
    Chip, Grid,
    MenuItem,
    Paper, Typography
} from '@mui/material';
import makeStyles from '@mui/styles/makeStyles';
import React, { useContext, useEffect, useRef, useState } from 'react';
import { useForm } from 'react-hook-form';
import { getAllCategory } from '../action/categoryAction';
import { createPoll } from '../action/votingAction';
import AddCandidateForm from '../component/AddCandidateForm';
import AddVoterForm from '../component/AddVoterForm';
import InputGrid from '../component/InputGrid';
import Layout from '../component/Layout';
import MyButton from '../component/UI/MyButton';
import { CategoryContext } from '../contextAPI/CategoryContext';
import { InfoContext } from '../contextAPI/InfoContext';
import { SnackbarContext } from '../contextAPI/SnackbarContext';

const useStyles = makeStyles(theme => ({
    header: {
        marginBottom: '40px'
    },
    headerContent: {
        textTransform: 'uppercase',
        fontWeight: '600',
    },
    paper: {
        padding: '16px',
        marginBottom: '16px',
        width: '900px',
        margin: '0 auto'
        // width: 'fit-content'
    },
    title: {
        marginBottom: '20px'
    },
    flex: {
        display: 'flex',
        justifyContent: 'space-between',
        marginBottom: '40px'
    },
    // inputItem: {
    //     marginBottom: theme.spacing(2),
    // },
}))

const CreateVoting = () => {
    const classes = useStyles();

    const { category, categoryDispatch } = useContext(CategoryContext);
    const { info } = useContext(InfoContext);
    const { openSnackbarDispatch } = useContext(SnackbarContext);
    const [candidate, setCandidate] = useState([]);
    const [voter, setVoter] = useState([]);
    const [type, setType] = useState("Biu quyt c s d");

    const {
        handleSubmit,
        control,
        setValue,
        setError,
        clearErrors,
        getValues,
        reset,
        watch,
        formState: { errors }
    } = useForm();

    const ThoiGianBatDau = useRef({});
    ThoiGianBatDau.current = watch("ThoiGianBatDau", "");
    const SoPhut = useRef({});
    SoPhut.current = watch("ThoiGianNhacNho", 0)

    const handleChangeSelect = (e) => {
        if (e.target.value != 0) {
            clearErrors(e.target.name)
        }
        setValue(e.target.name, e.target.value)
    }

    const handleChangeType = (e) => {
        setValue(e.target.name, e.target.value)
        setType(e.target.value);
    }

    const onSubmit = async (data) => {
        console.log(data);
        if (candidate.length == 0) {
            openSnackbarDispatch({
                type: 'SET_OPEN',
                payload: {
                    msg: "Bn cha chn ng c vin",
                    type: "error"
                }
            })
            return;
        }
        if (voter.length == 0) {
            openSnackbarDispatch({
                type: 'SET_OPEN',
                payload: {
                    msg: "Bn cha chn n v tham gia",
                    type: "error"
                }
            })
            return;
        }
        if (candidate.length == 1 && data.LoaiBieuQuyet == "Biu quyt c s d") {
            openSnackbarDispatch({
                type: 'SET_OPEN',
                payload: {
                    msg: "Biu quyt c s d phi chn t nht 2 ng c vin!",
                    type: "error"
                }
            })
            return;
        }

        data.UngCuVien = candidate.map(el => el.MaUngCuVien);
        data.NguoiThamGia = voter.map(el => el.MaNguoiThamGia);
        const res = await createPoll(data);
        if (res) {
            openSnackbarDispatch({
                type: 'SET_OPEN',
                payload: {
                    msg: " to cuc biu quyt",
                    type: "success"
                }
            })
            reset({
                TenBieuQuyet: "",
                ThoiGianBatDau: "",
                ThoiGianKetThuc: "",
                SoPhieuToiDa: 1,
            });
            setCandidate([]);
            setVoter([]);
        }
    }

    useEffect(() => {
        getAllCategory(categoryDispatch, "achievement");
        getAllCategory(categoryDispatch, "partycell");
        getAllCategory(categoryDispatch, "position");
        getAllCategory(categoryDispatch, "grade");
    }, [])

    return (
        <>
            <Layout sidebar>
                <div className={classes.header} >
                    <Typography className={classes.headerContent} variant="h5">
                        To Biu quyt
                    </Typography>
                </div>
                <Paper className={classes.paper} variant="outlined">
                    <Typography style={{ textTransform: 'uppercase', marginBottom: 30 }}>To biu quyt</Typography>
                    <Grid container className={classes.inputItem} alignItems="center">
                        <Grid item xs={4}>
                            <Typography>Tn biu quyt</Typography>
                        </Grid>
                        <Grid item xs={8}>
                            <InputGrid
                                noTitle
                                name="TenBieuQuyet"
                                control={control}
                                errors={errors}
                                rules={{
                                    required: "Vui lng nhp trng ny!",
                                }}
                            />
                        </Grid>
                    </Grid>
                    <Grid container className={classes.inputItem} alignItems="center">
                        <Grid item xs={4}>
                            <Typography>Mc ch</Typography>
                        </Grid>
                        <Grid item xs={8}>
                            <InputGrid
                                select
                                noTitle
                                onChange={handleChangeSelect}
                                name="MucDich"
                                defaultValue="0"
                                control={control}
                                errors={errors}
                                rules={{
                                    validate: value =>
                                        value != 0 || "Vui lng nhp trng ny!"
                                }}
                            >
                                <MenuItem value="0">Chn</MenuItem>
                                <MenuItem value="Khen thng">Khen thng</MenuItem>
                                <MenuItem value="K lut">K lut</MenuItem>
                                <MenuItem value="Khc">Khc</MenuItem>
                            </InputGrid>
                        </Grid>
                    </Grid>
                    <Grid container className={classes.inputItem} alignItems="center">
                        <Grid item xs={4}>
                            <Typography>Loi biu quyt</Typography>
                        </Grid>
                        <Grid item xs={8}>
                            <InputGrid
                                select
                                noTitle
                                onChange={handleChangeType}
                                name="LoaiBieuQuyet"
                                defaultValue="Biu quyt c s d"
                                control={control}
                                errors={errors}
                            >
                                <MenuItem value="Biu quyt c s d">Biu quyt c s d</MenuItem>
                                <MenuItem value="Biu quyt tn nhim">Biu quyt tn nhim</MenuItem>
                            </InputGrid>
                        </Grid>
                    </Grid>
                    {
                        type == "Biu quyt c s d" &&
                        <Grid container className={classes.inputItem} alignItems="center" >
                            <Grid item xs={4}>
                                <Typography>S phiu ti a</Typography>
                            </Grid>
                            <Grid item xs={8}>
                                <InputGrid
                                    noTitle
                                    type="number"
                                    defaultValue="1"
                                    name="SoPhieuToiDa"
                                    control={control}
                                    errors={errors}
                                    InputProps={{ inputProps: { min: 1 } }}
                                />
                            </Grid>
                        </Grid>
                    }

                    <Grid container className={classes.inputItem} alignItems="center">
                        <Grid item xs={4}>
                            <Typography>Thi gian bt u</Typography>
                        </Grid>
                        <Grid item xs={8}>
                            <InputGrid
                                noTitle
                                type="datetime-local"
                                name="ThoiGianBatDau"
                                control={control}
                                errors={errors}
                                rules={{
                                    required: "Vui lng nhp trng ny!",
                                }}
                            />
                        </Grid>
                    </Grid>
                    <Grid container className={classes.inputItem} alignItems="center">
                        <Grid item xs={4}>
                            <Typography>Thi gian kt thc</Typography>
                        </Grid>
                        <Grid item xs={8}>
                            <InputGrid
                                noTitle
                                type="datetime-local"
                                name="ThoiGianKetThuc"
                                control={control}
                                errors={errors}
                                rules={{
                                    required: "Vui lng nhp trng ny!",
                                    validate: value => {
                                        if (new Date(value) < new Date(ThoiGianBatDau.current))
                                            return "Thi gian kt thc phi ln hn thi gian bt u"
                                        if (new Date(value) < new Date())
                                            return "Thi gian kt thc phi hn thi gian hin ti"
                                        let date = new Date(value);
                                        date.setMinutes(date.getMinutes() - SoPhut.current);
                                        if (date < new Date()) {
                                            return " qu thi gian nhc nh"
                                        }
                                        return true
                                    }
                                }}
                            />
                        </Grid>
                    </Grid>
                    <Grid container className={classes.inputItem} alignItems="center" >
                        <Grid item xs={4}>
                            <Typography>Phm vi</Typography>
                        </Grid>
                        <Grid item xs={8}>
                            <InputGrid
                                noTitle
                                name="PhamVi"
                                control={control}
                                errors={errors}
                                rules={{
                                    required: "Vui lng nhp trng ny!",
                                }}
                            />
                        </Grid>
                    </Grid>
                    <Grid container className={classes.inputItem} alignItems="center" >
                        <Grid item xs={4}>
                            <Typography>Thi gian nhc nh (pht)</Typography>
                        </Grid>
                        <Grid item xs={8}>
                            <InputGrid
                                noTitle
                                type="number"
                                InputProps={{ inputProps: { min: 10 } }}
                                defaultValue="10"
                                name="ThoiGianNhacNho"
                                control={control}
                                errors={errors}
                                rules={{
                                    required: "Vui lng nhp trng ny!",
                                }}
                            />
                        </Grid>
                    </Grid>
                    <Grid container style={{ marginTop: '16px' }} >
                        <Grid item xs={4}>
                            <Typography>ng c vin</Typography>
                        </Grid>
                        <Grid item container xs={8}>
                            {
                                candidate.length > 0 &&
                                candidate.map(el =>
                                    <Grid item xs={12}>
                                        <Chip style={{ marginBottom: '12px' }}
                                            size="medium"
                                            varian="outlined"
                                            label={el.HoTen + " - " + el.MaUngCuVien}
                                        />
                                    </Grid>
                                )
                            }
                            <Grid item xs={12}>
                                <AddCandidateForm setCandidate={setCandidate} />
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid container style={{ marginTop: '16px' }} >
                        <Grid item xs={4}>
                            <Typography>n v tham gia</Typography>
                        </Grid>
                        <Grid item container xs={8}>
                            <Grid item xs={12}>
                                {
                                    voter.length > 0 &&
                                    voter.map(el =>
                                        <Chip style={{ marginBottom: '12px', marginRight: '4px' }}
                                            size="small"
                                            varian="outlined"
                                            label={el.HoTen + " - " + el.MaNguoiThamGia}
                                        />
                                    )
                                }
                            </Grid>
                            <Grid item xs={12}>
                                <AddVoterForm voter={voter} setVoter={setVoter} />
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid container style={{ marginTop: '16px' }} >
                        <Grid item xs={4}>
                        </Grid>
                        <Grid item xs={8}>
                            <MyButton onClick={handleSubmit(onSubmit)} info>To</MyButton>
                        </Grid>
                    </Grid>
                </Paper>
            </Layout>
        </>
    );
};

export default CreateVoting;